html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

.icon-decor {
  width: 1.54em;
  height: 1em;
  fill: #00AAEF;
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

body {
  background: #FFF;
  font-family: "Ubuntu",sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.textual {
  font-size: 16px;
  line-height: 1.5;
  color: #000;
}

.textual h2 {
  font-size: 32px;
  line-height: 1.1;
  font-weight: normal;
  margin-bottom: 1em;
}

.textual p {
  margin-bottom: 1em;
}

.textual ol {
  list-style: decimal;
  position: relative;
  left: -5px;
}

@media only screen and (max-width: 720px) {
  .textual ol {
    margin-left: 2em;
  }
}

.textual ol li {
  padding-left: 5px;
}

.textual ol, .textual ul {
  margin-bottom: 1em;
}

.textual_big {
  font-size: 24px;
  line-height: 1.3;
}

.textual_big a {
  text-decoration: none;
  color: #000;
}

.form__field {
  margin-bottom: 10px;
}

.input, .textarea {
  text-align: center;
  line-height: normal;
  height: 52px;
  background: #eeeeee;
  font-weight: 600;
  color: #393939;
  text-transform: uppercase;
  width: 100%;
}

.input:placeholder, .textarea:placeholder {
  font-size: 14px;
}

.textarea {
  height: 100px;
  padding: 20px;
  border: none;
}

.button {
  font-size: 14px;
  line-height: 52px;
  height: 52px;
  display: block;
  background: #1cabec;
  text-transform: uppercase;
  text-decoration: none;
  color: #fff;
  font-weight: 600;
  width: 100%;
}

.button_inline {
  display: inline-block;
  padding: 0 60px;
  width: auto;
}

.container {
  margin: 0 auto;
  max-width: 940px;
  *zoom: 1;
}

.container:after {
  content: " ";
  display: table;
  clear: both;
}

@media only screen and (max-width: 980px) {
  .container {
    margin: 0 20px;
  }
}
